*{
  padding: 0px;
  margin: 0px;
  font-family: 'Electrolize', sans-serif;
  ::-webkit-scrollbar
  {
    width: 6px;  /* for vertical scrollbars */
    height: 6px; /* for horizontal scrollbars */
  }
  
  ::-webkit-scrollbar-track
  {
    background: rgba(0, 0, 0, 0.1);
    border-radius: 3px;
  }
  
  ::-webkit-scrollbar-thumb
  {
    background: rgba(0, 0, 0, 0.5);
    border-radius: 3px;
  }

  ::-webkit-scrollbar-thumb:hover {
      background: rgba(110, 109, 109, 0.5);
  }
}

$text-col: white;
$device-border: 2px solid rgb(214, 214, 214);
$device-bgcolor: rgb(58, 58, 58);

#background {
  width: 100%;
  height: 100vh;
  background-color: rgb(165, 164, 164);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#calculator {
  width: 370px;
  padding: 5px;
  background-color: $device-bgcolor;
  border: $device-border;
  border-radius: 5px;
  box-shadow: 1px 1px 3px 3px rgb(95, 95, 95);
  margin-bottom: 15px;
}

#mainDisplay {
  width: 100%;
  height: 70px;
  overflow-y: hidden;
  #finalExp, #display {
      color: orange;
      display: inline;
      font-size: 15px;
  }
}

#currentDisplay {
  color: $text-col;
  width: 100%;
  height: 50px;
  font-size: 30px;
  overflow-y: hidden;
}

#controller {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: 60px 60px 60px 60px 60px;
  gap: 2px 2px;
  font-size: 20px;
  p {
      background-color: grey;
      color: $text-col;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid grey;
  }
  p:hover {
      border: 1px solid white;
  }
  P:active {
      color:rgb(165, 164, 164);
  }
  #clear {
      grid-column: 1/3;
      background-color: rgb(172,57,57);
  }
  #equals {
      grid-row: 4/6;
      grid-column: 4/5;
      background-color: rgb(0,68,102);
  }
  #zero {
      grid-column: 1/3;
  }
}

#history {
  color: rgb(172,57,57);
  font-weight: bold;
  width: 370px;
  height: 100px;
  display: flex;
  flex-direction: column;
  padding: 5px;
  margin-bottom: 25px;
  border: $device-border;
  border-radius: 5px;
  background-color: $device-bgcolor;
  box-shadow: 1px 1px 3px 3px rgb(95, 95, 95);
  p {
      height: 20px;
      font-size: 10px;
  }
  #scrollhistory {
      color: white;
      font-weight: 100;
      overflow-y: scroll;
      height: 100%;
      font-size: 12px;
      span {
          display: block;
      }
  }
}

#footer {
  text-align: center;
  font-size: 12px;
  #credit, #author {
      margin: 5px;
  }
  #credit {
      font-style: italic;
  }
  #author {
      font-weight: bold;
      a {
          color: rgb(172,57,57);
          text-decoration: none;
      }
      a:hover {
          color: rgb(172,57,57);
          text-decoration: none;
          font-weight: 500;
          background-color: orange;
          border-radius: 5px;
          box-shadow: 1px 1px 5px 2px rgb(248, 196, 100);
      }
  }
}